@use "../../base/variables";
@use "../../base/mixins";

.formulario{
    margin: 5rem auto 2rem auto;
    background-color: variables.$blanco;
    border-radius: 5rem;
    box-shadow: 0px 3px 7px 0px variables.$gris;
    padding: 3rem 0;
    @include mixins.desktop{ 
        display: flex;
        justify-content: space-between;
        max-width: 90rem;
        padding: 0;
    }
    &__campo{
        padding: 1rem 2rem;
        display: flex;
        flex-direction: column;
        &:nth-child(1){
            @include mixins.desktop{ 
                padding-left: 4rem;
            }
        }
        &:hover{
            background-color: #f0f0f0;
            border-radius: 5rem;
            box-shadow: 0 6px 20px rgb(0, 0, 0, .4);
        }
    }
    &__label{
        font-size: 1.2rem;
        font-weight: 900;
    }
    &__input{
        border: none;
        width: 100%;
        color: variables.$gris;
    }
    &__submit{
        display: block;
        height: 4rem;
        border: none;
        border-radius: 2rem;
        background-image: radial-gradient( circle, #ff385c 0%, #bd1e59 100% );
        padding: 0 2rem;
        color: variables.$blanco;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 1.2rem;

    }
}

